<template>
    <div class="main" :style="mainImg">
        <div class="mainbox">
            <div class="mainbox-item">
            <div class="mainbox-item-img" 
                v-for="(item, index) in list" :key="index" 
                :style="getItemImg(index)"
                @click="widthClick(index)"
                ref="itemDom"
            />
        </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'mainbox',
    props:['list'],
    data() {
        return {
            mainImg: '',
        }
    },
    mounted() {
        this.$refs.itemDom[0].classList.add("active");
        this.mainImg = 'background: url(' + this.list[0].img + ')';
    },
    methods: {
        getItemImg(i) {
            return 'background: url(' + this.list[i].img + '); background-size: cover; background-position: center';
        },
        widthClick(i) {
            this.getClass();
            this.$refs.itemDom[i].classList.add("active");
            this.mainImg = 'background: url(' + this.list[i].img + ')';     
        },
        getClass() {
            for(let i=0; i<this.list.length ; i++) {
                this.$refs.itemDom[i].setAttribute("class","mainbox-item-img");         
            }
        }
    },
}
</script>

<style scoped>


/* * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
} */
.main {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    transition: all 1s ease;
    background-size: 100%;
    background-position: center;
    z-index: -2;
}
.main:after {
    content: "";
    width: 100vw;
    height: 100vh;
    position: absolute;
    left: 0;
    top: 0;
    background: inherit;
    filter: blur(5px);
    z-index: -1;
}

.mainbox {

    width: 80vw;
    height: 85vh;
    margin: 10vh auto 5vh;
}

.mainbox-item {
    display: flex;
}

.mainbox-item-img {
    height: 85vh;
    width: 10vw;
    border-radius: 14px;
    background: skyblue;
    border: 1px solid #000;
    transition: all 1s ease;
}
.active {
    width: 40vw;
}

</style>